@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Inter', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -ms-overflow-style: none;
  font-smooth: always;
  scrollbar-width: none;
  overflow-x: hidden;
}
::-webkit-scrollbar{
  display: none;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
*{
  padding: 0;
  margin:0;
}
.about{height:100vh;margin:200px 0px 0px 100px}.about-heading{opacity:0;-webkit-clip-path:inset(100% 0% 0% 0%);clip-path:inset(100% 0% 0% 0%);-webkit-transform:translateY(100%);transform:translateY(100%);transition:all 1s cubic-bezier(0.77, 0, 0.175, 1)}.about-heading>span{font-size:4rem;font-weight:500}.about-heading.is-reveal{opacity:1;-webkit-transform:translateY(0%);transform:translateY(0%);-webkit-clip-path:inset(0% 0% 0% 0%);clip-path:inset(0% 0% 0% 0%)}.about-info{margin-top:50px;width:80%;height:250px;opacity:0;-webkit-clip-path:inset(100% 0% 0% 0%);clip-path:inset(100% 0% 0% 0%);-webkit-transform:translateY(100%);transform:translateY(100%);transition:all 1s cubic-bezier(0.77, 0, 0.175, 1)}.about-info>span{letter-spacing:0.1em;font-size:1rem}.about-info.is-reveal{opacity:1;-webkit-transform:translateY(0%);transform:translateY(0%);-webkit-clip-path:inset(0% 0% 0% 0%);clip-path:inset(0% 0% 0% 0%)}

.contact{display:flex;flex-direction:column;justify-content:center;background-color:black;width:100%;height:31vh}.contact a{text-decoration:none}.contact-top-links{display:flex;flex-direction:row;justify-content:center}.contact-top-links-items{display:flex;flex-direction:row;justify-content:space-between;width:75%;margin-top:20px}.contact-top-links-items .LinkedIn>a,.contact-top-links-items .Github>a,.contact-top-links-items .Email>a{font-family:'Oswald';font-weight:bolder;color:transparent;-webkit-text-stroke:2px #787878;font-size:3rem;letter-spacing:0.4rem;transition:color 0.4s ease}.contact-top-links-items .LinkedIn>a:hover,.contact-top-links-items .Github>a:hover,.contact-top-links-items .Email>a:hover{color:#787878}

.main-container{display:flex;flex-direction:row;height:90vh}.main-left{width:60%;padding-top:180px;padding-left:150px;-webkit-animation:fromLeft 2s;animation:fromLeft 2s}.main-left-roles{color:#636363;padding:0;margin:0;font-size:1rem;font-weight:600}.main-left-name{color:#303030}.main-left-name-first,.main-left-name-last{padding:0;margin:0}.main-left-name-first>span,.main-left-name-last>span{font-size:5.2rem;font-weight:500}.main-left-name-last{margin-top:-14px}.main-right{width:50%;margin-top:12px;margin-right:150px}@-webkit-keyframes fromLeft{from{-webkit-transform:translate(-100%, 0);transform:translate(-100%, 0);opacity:0}to{-webkit-transform:translate(0%, 0);transform:translate(0%, 0);opacity:1}}@keyframes fromLeft{from{-webkit-transform:translate(-100%, 0);transform:translate(-100%, 0);opacity:0}to{-webkit-transform:translate(0%, 0);transform:translate(0%, 0);opacity:1}}

.navbar{box-sizing:border-box;padding:30px 30px 40px;width:100%;height:10vh;-webkit-animation:fromUp 2s;animation:fromUp 2s}.navbar-container{display:flex;flex-direction:row;justify-content:space-between}.navbar-left{width:50%;display:flex;flex-direction:row;align-items:center}.navbar-left-logo{display:flex;flex-direction:column;align-items:center}.navbar-left-logo>span{font-family:'Inter';margin-left:20px;font-size:1.5rem;font-weight:600;-webkit-transform:rotate(0deg);transform:rotate(0deg);transition:-webkit-transform 800ms;transition:transform 800ms;transition:transform 800ms, -webkit-transform 800ms}.navbar-left-logo>span:hover,.navbar-left-logo>span:active{cursor:pointer;-webkit-transform:rotate(360deg);transform:rotate(360deg)}.navbar-right{width:50%;display:flex;margin-top:5px;align-items:center;justify-content:flex-end}.navbar-right-projects,.navbar-right-about,.navbar-right-contact{display:flex;flex-direction:row;align-items:center;margin-right:100px;border-bottom:2px solid transparent;height:100%;transition:border 600ms}.navbar-right-projects>a,.navbar-right-about>a,.navbar-right-contact>a{color:black;text-decoration:none;font-size:1rem;font-weight:600}.navbar-right-projects:hover,.navbar-right-about:hover,.navbar-right-contact:hover{cursor:pointer;border-bottom:2px solid #646464}@-webkit-keyframes fromUp{from{-webkit-transform:translate(0, -100%);transform:translate(0, -100%);opacity:0}to{-webkit-transform:translate(0%, 0);transform:translate(0%, 0);opacity:1}}@keyframes fromUp{from{-webkit-transform:translate(0, -100%);transform:translate(0, -100%);opacity:0}to{-webkit-transform:translate(0%, 0);transform:translate(0%, 0);opacity:1}}

.project1{display:flex;justify-content:center;height:100%;position:relative;width:100vw}.project1 img{-webkit-clip-path:inset(100% 0% 0% 0%);clip-path:inset(100% 0% 0% 0%);transition:-webkit-clip-path 1s cubic-bezier(0.77, 0, 0.175, 1);transition:clip-path 1s cubic-bezier(0.77, 0, 0.175, 1);transition:clip-path 1s cubic-bezier(0.77, 0, 0.175, 1), -webkit-clip-path 1s cubic-bezier(0.77, 0, 0.175, 1)}.project1 img.is-reveal{-webkit-clip-path:inset(0% 0% 0% 0%);clip-path:inset(0% 0% 0% 0%)}.project1-wrapper{width:100%;display:grid;grid-template-columns:3fr 6fr;grid-gap:10%;align-items:center;justify-content:center}.project1-name{position:absolute;z-index:1;top:50%;left:15%}.project1-name>h6{font-size:6rem;font-weight:600;margin-bottom:10px;color:silver}.project1-name>span{font-family:'Oswald', sans-serif;font-size:4.5rem;font-weight:500;letter-spacing:0.1em;color:transparent;-webkit-text-stroke:2px #a3a3a3}.project1-image1{height:100%;margin-top:100px}.project1-image1 img{z-index:0;width:430px;height:35vh}.project1-image2{margin-top:150px;margin-right:10px;display:flex;flex-direction:row;justify-content:flex-end}.project1-image2 img{z-index:0;width:800px;height:58vh}

.project2{display:flex;justify-content:center;height:100%;position:relative;width:100vw}.project2-wrapper{display:flex;flex-direction:row;justify-content:flex-end;width:100%}.project2-name{z-index:1;position:absolute;top:44%;left:20%}.project2-name>h6{font-size:5.6rem;font-weight:600;margin-bottom:10px;color:silver}.project2-name>span{font-family:'Oswald', sans-serif;font-size:4.5rem;font-weight:500;letter-spacing:0.2em;color:transparent;-webkit-text-stroke:2px #a3a3a3}.project2-image2{display:flex;flex-direction:row;justify-content:flex-end;align-items:center}.project2-image2 img{-webkit-transform:scale(1);transform:scale(1);transition:-webkit-transform 1s;transition:transform 1s;transition:transform 1s, -webkit-transform 1s;z-index:0;width:800px;height:55vh}.project2-image2 img:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}

.otherprojects{display:flex;width:300vw;height:100%;justify-content:center}.otherprojects-wrapper{display:flex;flex-direction:row;width:100vw;justify-content:flex-end}.otherprojects-name1{z-index:1;margin-top:350px;margin-right:-100px}.otherprojects-name1>h6{font-size:5.6rem;font-weight:600;margin-bottom:10px;color:silver}.otherprojects-name1>span{font-family:'Oswald', sans-serif;font-size:4.5rem;font-weight:500;letter-spacing:0.2em;color:transparent;-webkit-text-stroke:2px #a3a3a3}.otherprojects-name2{z-index:1;margin-top:350px;margin-right:-40px}.otherprojects-name2>h6{font-size:5.6rem;font-weight:600;color:silver}.otherprojects-name2>span{font-family:'Oswald', sans-serif;font-size:4.5rem;font-weight:500;letter-spacing:0.2em;color:transparent;-webkit-text-stroke:2px #a3a3a3}.otherprojects-name3{z-index:1;margin-top:350px;margin-right:-10px}.otherprojects-name3>h6{font-size:5.6rem;font-weight:600;margin-bottom:10px;color:silver}.otherprojects-name3>span{font-family:'Oswald', sans-serif;font-size:4.5rem;font-weight:500;letter-spacing:0.2em;color:transparent;-webkit-text-stroke:2px #a3a3a3}.otherprojects-image1{display:flex;flex-direction:column;justify-content:center;align-items:flex-end;height:100vh}.otherprojects-image1 img{-webkit-transform:scale(1);transform:scale(1);transition:-webkit-transform 1s;transition:transform 1s;transition:transform 1s, -webkit-transform 1s;width:800px;height:58vh !important}.otherprojects-image1 img:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.otherprojects-image2{display:flex;flex-direction:column;justify-content:center;align-items:flex-end;height:100vh}.otherprojects-image2 img{-webkit-transform:scale(1);transform:scale(1);transition:-webkit-transform 1s;transition:transform 1s;transition:transform 1s, -webkit-transform 1s;width:350px;height:78vh !important;margin-right:100px}.otherprojects-image2 img:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.otherprojects-image3{display:flex;flex-direction:column;justify-content:center;align-items:flex-end;height:100vh;margin-right:80px}.otherprojects-image3 img{-webkit-transform:scale(1);transform:scale(1);transition:-webkit-transform 1s;transition:transform 1s;transition:transform 1s, -webkit-transform 1s;width:780px;height:52vh !important}.otherprojects-image3 img:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}

.projects{background-color:#1a1a1a;height:101vh;width:500vw;display:flex;flex-direction:row}

.loader-component{font-family:'Oswald';position:fixed;top:0;right:0;bottom:0;left:0;background-color:white;z-index:1;color:#acacac;display:flex;justify-content:center;align-items:center;height:100vh;font-size:8rem;color:transparent;-webkit-text-stroke:10px #d4d4d4;letter-spacing:-0.5rem}

.construction{display:flex;flex-direction:column}.construction-title{margin-left:20px}.construction-title>span{font-size:2rem;font-weight:800}.construction-detail{margin-left:20px;margin-top:10px}.construction-detail>span{font-size:1rem;font-weight:500}

